aF;AUjbA;EXCA;IAoBI,eAAA;IACA,iBAAA;ECDF;AAkaF","sourcesContent":["\n.container {\n  width: 100%;\n  margin: 0 auto;\n\n  padding-top: 0;\n  padding-bottom: 0;\n\n  // max-width: 1200px;\n\n  // max-width: clamp(352px, -0.617rem + 97vw, 1376px)\n\n  @include mobile {\n    max-width: 376px;\n  }\n\n  @include tablet {\n    max-width: 768px;\n  }\n\n  @include desktop {\n    padding: 0 32px;\n    max-width: 1264px;\n  }\n}","@import '~normalize.css';\n\n// utils\n@import '../style/utils/mixin';\n@import '../style/utils/container';\n@import '../style/utils/fonts';\n@import '../style/utils/is-hidden';\n@import '../style/utils/reset';\n@import '../style/utils/vars';\n@import '../style/common';\n\n// layout\n@import '../style/header';\n@import '../style/footer';\n\n// modals\n// @import '../style/modals/burger-modal';\n@import '../style/modals/pop-up-modal';\n\n\n\n// partials\n@import '../style/privacy';\n\n\n\n\n\n","@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-Regular.ttf') ;\n    font-weight: 400;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-Medium.ttf') ;\n    font-weight: 500;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-SemiBold.ttf') ;\n    font-weight: 600;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-Bold.ttf') ;\n    font-weight: 700;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-ExtraBold.ttf') ;\n    font-weight: 800;\n}\n\n  // @font-face {\n  //   font-family: 'Roboto';\n  //   src: url('../../fonts/Roboto-Regular.woff') format('woff'),\n  //     url('../../fonts/Roboto-Regular.woff2') format('woff2');\n  //   font-weight: 400;\n  // }",".is-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    margin: -1px;\n    border: 0;\n    padding: 0;\n\n    white-space: nowrap;\n    clip-path: inset(100%);\n    clip: rect(0 0 0 0);\n    overflow: hidden;\n}","*{\n    box-sizing: border-box;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\nimg {\n    display: block;\n    max-width: 100%;\n    height: auto;\n}\n\na {\n    text-decoration: none;\n    color: currentColor;\n    display: block;\n}\n\nul,ol {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n.link {\n    text-decoration: none;\n}\n\n.list {\n    text-decoration: none;\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\nsvg{\n    fill: currentColor;\n}\n","html,\nbody {\n  font-family: $main-font;\n  background-color: #fff;\n\n  font-size: 16px;\n  font-weight: 400;\n  line-height: 1.3;\n  color: $black;\n  \n  margin: 0 auto;\n  width: 100%;\n\n  scroll-behavior: smooth;\n}\n//   clamp(min, current, max)\n//   window 1440px / max font-size = 44px\n//   1vw = 14.4px\n//   44px / 14.4px = 3.05vw\nbody.hidden{\n  overflow: hidden;\n}\n\naddress {\n  font-style: normal;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\nsection{\n  padding: 80px 0;\n}\n\nh1,h2{\n  font-size: 50px;\n  line-height: 1.1;\n  font-weight: 800;\n}\n\n// btn hover focus active \n.hfa{\n  transition: $transition;\n\n  &:hover,&:focus{\n    color: $accent;\n    border: 2px solid $accent;\n    background-color: $white;\n    & svg{\n      transition: $transition;\n      color: $accent;\n    }\n  }\n  &:active{\n    color: $accent;\n    border: 2px solid $accent;\n    background-color: $white;\n    & svg{\n      transition: $transition;\n      color: $accent;\n    }\n  }\n}\n\n\n.flex{\n  display: flex;\n}\n.al-c{\n  align-items: center;\n}\n.jc{\n  justify-content: center;\n}\n.jb{\n  justify-content: space-between;\n}\n.gap-10{\n  gap: 10px;\n}\n.gap-20{\n  gap: 20px;\n}\n.gap-60{\n  gap:60px;\n}\n.gap-80{\n  gap: 80px;\n}\n\n// opacity\n.opacity-50{\n  opacity: 50%;\n}\n.opacity-60{\n  opacity: 60%;\n}\n\n\n//margin\n.mb-15{\n  margin-bottom: 15px;\n}\n.mb-20{\n  margin-bottom: 20px;\n}\n.mb-30{\n  margin-bottom: 30px;\n}\n.mb-40{\n  margin-bottom: 40px;\n}\n.mb-50{\n  margin-bottom: 50px;\n}\n\n//btn / link\n.btn{\n  max-width: max-content;\n  padding: 16px 48px;\n  background-color: $accent;\n  color: $white;\n  font-size: 16px;\n  line-height: 1.3;\n  font-weight: bold;\n  border: 2px solid $accent;\n  border-radius: 20px;\n  cursor: pointer;\n\n  &-svg{\n    margin-left: 10px;\n  }\n}\n\n.link-btn{\n  max-width: max-content;\n  color: $accent;\n  font-size: 15px;\n  line-height: 1.5;\n  font-weight: bold;\n\n  position: relative;\n\n  \n\n  &::before{\n    content: '';\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n    height: 2px;\n    background-color: $accent;\n    opacity: 0;\n\n    transition: $transition;\n  }\n\n  &:hover, &:focus{\n    &::before{\n      opacity: 1;\n    }\n  }\n\n  &:active{\n    &::before{\n      opacity: 0;\n    }\n  }\n\n  &-svg{\n    margin-left: 10px;\n  }\n\n}\n\n\n\n","// font family\n$main-font:\"Prompt\", sans-serif;\n\n// transition\n$transition: all 0.3s ease-out;\n$transition-burger: all 0.5s ease-out;\n$transition-questions: all 1s linear;\n\n$white: #F6F7F7;\n$black:#1E1E1E;\n$accent:#5646CC;\n$error:#ff1e0e;\n\n// modal\n$modal-bgd: #00000066;\n\n\n\n",".header{\n    margin-top: 20px;\n    margin-bottom: 10px;\n}\n.navigation{\n\n    position: relative;\n    &::after{\n        content: '';\n        position: absolute;\n        top: 49px;\n        left: 0;\n        width: 100%;\n        height: 1px;\n        background-color: #1E1E1E;\n        opacity: 0.15;\n    }\n}\n.header-nav{\n    margin-left: auto;\n    margin-right: 40px;\n\n    & .item{\n        padding-top: 10px;\n        padding-bottom: 9px;\n\n    }\n\n    & a{\n        transition: $transition;\n    }\n    & a:hover, a:focus{\n      color: $accent;\n    }\n}\n\n.change-lang{\n    font-weight: 600;\n    padding-top: 10px;\n    padding-bottom: 9px;\n    cursor: pointer;\n}",".footer{\n    padding-top: 80px;\n    padding-bottom: 20px;\n\n    font-size: 14px;\n    line-height: 1.5;\n\n    &-nav{\n        & a{\n            transition: $transition;\n        }\n        & a:hover, a:focus{\n          color: $accent;\n        }\n    }\n\n    &-privacy{\n        position: relative;\n\n        color: $black;\n        font-size: 14px;\n        line-height: 1.5;\n        font-weight: 400;\n        cursor: pointer;\n\n        \n        transition: $transition;\n\n        &::before{\n          content: '';\n          position: absolute;\n          bottom: 0;\n          left: 0;\n          width: 100%;\n          height: 2px;\n          background-color: $accent;\n          opacity: 0;\n      \n          transition: $transition;\n        }\n      \n        &:hover, &:focus{\n            color: $accent;\n\n          &::before{\n            opacity: 1;\n          }\n        }\n        &:active{\n            &::before{\n                opacity: 0;\n            }\n        }\n    }\n}",".modal-bgd{\n    display: none;\n    justify-content: center;\n    align-items: center;\n\n    width: 100%;\n    height: 100%;\n    position: fixed;\n    top: 0;\n    left: 0;\n    background: $modal-bgd;\n\n    opacity: 0;\n    pointer-events: none;\n\n    transition: $transition;\n    \n\n    z-index: 11;\n}\n\n.pop-up{\n    \n    &.modal-active {\n        opacity: 1;\n        pointer-events: all;\n    }\n\n    &-wrapper{\n        padding: 30px;\n        background-color: $white;\n        border-radius: 20px;\n        font-size: 15px;\n        line-height: 1.3;\n    \n        max-width: 590px;\n    }\n\n    &-title{\n        font-size: 22px;\n        line-height: 1.1;\n        text-align: center;\n    }\n\n    &-sub-title{\n\n    }\n\n    &-list-btn{\n        display: flex;\n        flex-wrap: wrap;\n        gap: 20px;\n        margin-bottom: 30px;\n\n        & li{\n            width: calc(50% - 10px);\n        }\n    }\n\n    &-btn{\n        width: 100%;\n        max-width: 255px;\n        cursor: pointer;\n    }\n\n    &-accent-btn{\n        width: 100%;\n        max-width: 255px;\n        cursor: pointer;\n        background-color: $white;\n        color: $accent;\n\n    }\n\n    &-list-link{\n        display: flex;\n        flex-direction: column;\n        gap: 15px;\n    }\n\n    &-link{\n        color: $accent;\n        text-decoration: underline;\n        transition: $transition;\n        \n        &:hover,&:focus,&:active{\n            text-decoration: none;\n        }\n    }\n}\n\n\n\n\n",".privacy{\n    padding-top: 60px;\n    padding-bottom: 80px;\n\n    &-list{\n        margin-bottom: 20px;\n        max-width: 1098px;\n        width: 100%; \n    }\n    &-list:last-child{\n        margin-bottom: 0px;\n    }\n\n    .list-title{\n        margin-bottom: 20px;\n    }\n\n}",null],"sourceRoot":""}]);
